import Vue from "vue";
import Vuex from "vuex";
import Axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    status: "",
    token: localStorage.getItem("_tk") || "",
    privateKey: localStorage.getItem("_pv"),
    publicKey: localStorage.getItem("_pb"),
    accessUser: localStorage.getItem("_au"),
    auth: {
      message: "",
    },
    currentUser: "",
    isLoggedIn: false,
    acceptor: null,
  },
  mutations: {
    set_auth_keys(state, payload) {
      state.privateKey = payload.privateKey;
      state.publicKey = payload.publicKey;
      state.token = payload.token;
    },

    auth_success(state, auth) {
      state.status = "success";
      state.currentUser = auth.user;
      state.isLoggedIn = true;
      state.accessUser = auth.access_user;
      state.acceptor = auth.accepter;
    },
    auth_error(state, payload) {
      state.status = payload.statusCode;
      state.auth.message = payload.error;
    },
    logout(state) {
      state.status = "";
      state.token = "";
      state.publicKey = "";
      state.privateKey = "";
    },
  },
  actions: {
    //////////////////////////////////
    // Start Token
    getToken({ commit }, user) {
      return new Promise((resolve, reject) => {
        Axios({
          url: "token",
          data: { l: process.env.VUE_APP_LICENCE },
          method: "POST",
        })
          .then((resp) => {
            const token = resp.data?.ASREVIRA?.token;
            const privateKey = resp.data?.ASREVIRA?.privateKey;
            const publicKey = resp.data?.ASREVIRA?.publicKey;
            localStorage.setItem("_tk", token);
            localStorage.setItem("_pv", privateKey);
            localStorage.setItem("_pb", publicKey);
            commit("set_auth_keys", { privateKey, publicKey, token });
            Axios.defaults.headers.common["token"] = token;
            Axios.defaults.headers.common["key_public"] = publicKey;
            Axios.defaults.headers.common["Content-Type"] = "application/json";
            Axios.defaults.headers.common["Access-Control-Allow-Origin"] = "*";

            this.dispatch("login", { privateKey, user }).then((res) => {
              resolve(res.data);
            });
          })
          .catch((err) => {
            let error = err.response?.data?.ASREVIRA?.message;
            let statusCode = err.response.status;
            commit("auth_error", { error, statusCode });
            localStorage.removeItem("token");
            reject(err);
          });
      });
    },
    //  End  Token
    //////////////////////////////////

    //////////////////////////////////
    // Start check user and password for login
    login({ commit }, payload) {
      return new Promise((resolve, reject) => {
        Axios({
          url: payload.privateKey + "/login",
          data: payload.user,
          method: "POST",
        })
          .then((result) => {
            const accessUser = result.data?.ASREVIRA?.access_user;
            localStorage.setItem("_au", accessUser);
            Axios.defaults.headers.common["access_user"] = accessUser;
            commit("auth_success", result.data.ASREVIRA);
            resolve(result);
          })
          .catch((err) => {
            let error = err.response?.data?.ASREVIRA?.message;
            let statusCode = err?.response?.status;
            commit("auth_error", { error, statusCode });
            localStorage.removeItem("token");
            reject(err);
          });
      });
    },
    //  End check user and password for login
    //////////////////////////////////

    //////////////////////////////////
    // Start user logout : Remove token publicKey PrivateKey form localStorage & Axios Header
    logout({ commit }) {
      return new Promise(() => {
        commit("logout");
        localStorage.removeItem("_tk");
        localStorage.removeItem("_pv");
        localStorage.removeItem("_pb");
        localStorage.removeItem("usr");
        delete Axios.defaults.headers.common["token"];
        delete Axios.defaults.headers.common["key_public"];
      });
    },
    //  End user logout : Remove token publicKey PrivateKey form localStorage & Axios Header
    //////////////////////////////////
    setUserFromToken({ commit }, data) {
      commit("auth_success", data);
    },
  },
  modules: {},

  getters: {
    //////////////////////////////////
    // Start Check User Login
    isLoggedIn: (state) => state.token && state.publicKey && state.privateKey,
    //  End  Check User Login
    //////////////////////////////////

    //////////////////////////////////
    // Start check admin user
    // isAdmin: (state) => state.currentUser.role === ''

    //  End  check admin user
    //////////////////////////////////


  },
});
