<template>
  <v-form ref="thisForm" lazy-validation @submit.prevent="validate">
    <v-card class="pa-16">
      <CardTitle label="ویرایش نماینده" color="warning"/>
      <v-row>
        <Title label="مشخصات نماینده" />
      </v-row>
      <v-row>
        <Farsi md="4" label="نام و نام خانوادگی" v-model="itemData.name" />
        <Farsi md="4" label="نام پدر" v-model="itemData.fatherName" />
        <Number md="4" label="تلفن همراه" v-model="itemData.mobile" />
      </v-row>
      <v-row>
        <Number md="4" label="کد ملی" v-model="itemData.nationalCode" />
        <Number
          md="4"
          label="شماره شناسنامه"
          v-model="itemData.certificateNumber"
        />
        <Email md="4" label="پست الکترونیکی" v-model="itemData.email" />
      </v-row>
      <v-row>
        <Country md="3" v-model="itemData.country" />
        <Province
          md="3"
          v-model="itemData.state"
          :countryId="itemData.country"
        />
        <City md="3" v-model="itemData.city" :stateId="itemData.state" />
        <Number md="3" label="کد پستی" v-model="itemData.postalCode" />
      </v-row>
      <v-row>
        <Textarea
          label="آدرس محل سکونت"
          v-model="itemData.address"
          className="rounded-lg-1"
        />
      </v-row>
      <v-row>
        <English md="4" label="تلفن ثابت" v-model="itemData.phone" />
        <v-col cols="12" md="4">
          <v-select
            item-text="name"
            item-value="value"
            label="وضعیت"
            :rules="[rules.required]"
            :items="allStatus"
            v-model="itemData.status"
            required
          />
        </v-col>
        <v-col cols="12" md="4">
          <v-select
            label="نوع نماینده"
            item-text="name"
            item-value="id"
            :rules="[rules.required]"
            :items="alltypes"
            v-model="itemData.type"
            required
          />
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12" md="8">
          <v-row>
            <English md="6" label="نام کاربری" v-model="itemData.username" />
            <Number
              md="6"
              label="کد کاربری معرف"
              v-model="itemData.referenceCode"
            />
          </v-row>
        </v-col>
        <v-col cols="12" md="4">
          <v-row>
            <DatePicker v-model="itemData.birthdate" />
          </v-row>
        </v-col>
      </v-row>

      <v-row>
        <v-radio-group row v-model="itemData.gender" required>
          <v-radio label="مرد" value="0" />
          <v-radio label="زن" value="1" />
        </v-radio-group>
      </v-row>

      <v-row>
        <Title label="بارگذاری تصاویر مدارک" />
      </v-row>
      <v-row>
        <ImageUploader
          :imgSrc="itemData.nationalCardImage"
          imgName="nationalCardImage"
          md="4"
          required
        >
          <template v-slot:header> بارگذاری تصویر کارت ملی </template>
        </ImageUploader>
        <ImageUploader
          :imgSrc="itemData.birthCertificateImage"
          imgName="birthCertificateImage"
          md="4"
          required
        >
          <template v-slot:header> بارگذاری تصویر شناسنامه </template>
        </ImageUploader>
      </v-row>
      <v-row class="mt-16">
        <v-col cols="12" class="d-flex justify-center">
          <Btn
            label="ویرایش اطلاعات"
            color="#57cf8f"
            dark
            class="mt-16"
            type="submit"
          />
        </v-col>
      </v-row>
    </v-card>
  </v-form>
</template>

<script>
import Title from "@/components/Title";
import Farsi from "@/components/FarsiText";
import Number from "@/components/EnglishNumber";
import Email from "@/components/Email";
import Country from "@/components/Country";
import Province from "@/components/Province";
import City from "@/components/City";
import Textarea from "@/components/Textarea";
import English from "@/components/English";
import DatePicker from "@/components/DatePicker";
import ImageUploader from "@/components/ImgUploader";
import Btn from "@/components/Btn";
import CardTitle from "@/components/CardTitle";
import { EventBus } from "@/mixins/EventBus.js";
export default {
  name: "Edit",
  components: {
    Title,
    Farsi,
    Number,
    Email,
    Country,
    Province,
    City,
    Textarea,
    English,
    DatePicker,
    ImageUploader,
    Btn,
    CardTitle,
  },
  data() {
    return {
      allCountry: [],
      allStates: [],
      allCities: [],
      allStatus: [
        { id: 0, name: "غیر فعال", value: -1 },
        { id: 1, name: "در حال رسیدگی", value: 0 },
        { id: 2, name: "فعال", value: 1 },
      ],
      alltypes: [
        { id: "0", name: "تکی" },
        { id: "1", name: "استانی" },
      ],
      itemData: {},
      rules: {
        required: (value) => !!value || "این فیلد الزامی است",
      },
    };
  },
  methods: {
    //////////////////////////////////
    // Start check form validation

    validate() {
      if (this.$refs.thisForm.validate()) {
        this.updateForm();
      }
    },

    //  End  check form validation
    //////////////////////////////////

    //////////////////////////////////
    // Start send data
    updateForm() {
      let formData = new FormData();
      let oFormData = this.itemData;
      for (var key in oFormData) {
        if (oFormData[key] != null) formData.append(key, oFormData[key]);
      }
      
      this.$http
        .put(
          `${this.$privateKey}/agent/${this.$route.params.id}`,
          this.itemData
        )
        .then((res) => {
          this.dialog = true;
          this.routerToList();
          return res;
        })
        .catch((err) => {
          this.error = true;
          return err;
        });
    },
    //  End  send data
    //////////////////////////////////
    //////////////////////////////////
    // Start router to list
    routerToList() {
      setTimeout(() => {
        this.$router.push(`/admin/agent/list`);
      }, 600);
    },
    //  End  router to list
    //////////////////////////////////

    //////////////////////////////////
    // Start Get Tabel Data Item

    getItemData() {
      this.$http
        .get(`${this.$privateKey}/agent/${this.$route.params.id}`)
        .then((res) => {
          this.itemData = res.data.ASREVIRA;
          this.itemData.country = parseInt(res.data.ASREVIRA.country);
          this.itemData.state = parseInt(res.data.ASREVIRA.state);
          this.itemData.city = parseInt(res.data.ASREVIRA.city);
          // this.itemData.city = this.findCity(this.itemData.city);
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //  End  Get Tabel Data Item
    //////////////////////////////////

    // findCity(id) {
    //   return this.allCities.find((city) => city.id == id);
    // },
  },
  mounted() {
    EventBus.$on("selectImg", (data) => {
      this.itemData[data.name] = data.src;
    });
  },
  async beforeCreate() {
    await this.getItemData();
  },
};
</script>
