<template>
  <v-data-table
    class="elevation-1 data-table grey--text text--darken-2"
    hide-default-footer
    :headers="tableHeader"
    :items="tableData"
    :search="listSearch"
    :items-per-page="listCount"
    :page="listPage"
    :loading="isLoading"
    loading-text="در حال بار گذاری ..."
  >
    <template v-slot:top>
      <div style="background-color: #e9e9e9; position: relative">
        <CardTitle label="کامنت ها" color="#f1c60a" />
        <v-row>
          <v-col cols="9" sm="4" offset-sm="6">
            <v-text-field
              v-model="listSearch"
              label="جستجو"
              append-icon="mdi-magnify"
            />
          </v-col>
          <v-col cols="3" sm="2">
            <v-select
              class="mx-5"
              :items="listCountsComputed"
              v-model="listCount"
            />
          </v-col>
        </v-row>
      </div>
    </template>

    <template slot="body" slot-scope=""> </template>

    <template v-slot:item.icon>
      <v-icon>mdi-comment</v-icon>
    </template>

    <template v-slot:item.status="{ item }">
      <span v-show="item.status == -1" class="red--text text--accent-2"
        >رسیدگی نشده</span
      >
      <span v-show="item.status == 0" class="yellow--text text--darken-3"
        >رسیدگی شده</span
      >
      <span v-show="item.status == 1" class="green--text text--lighten-1"
        >در حال رسیدگی</span
      >
    </template>

    <template v-slot:item.action>
      <v-row justify="center">
        <v-col cols="12" md="10" lg="8" xl="6">
          <v-row>
            <ActionBtn iconEye lg="6" />
          </v-row>
        </v-col>
      </v-row>
    </template>
    <template v-slot:item.createdAt="{ createdAt }">
      <span class="text-truncate">
        {{ jalali(createdAt) }}
      </span>
    </template>

    <template v-slot:footer>
      <div class="vira-data-table-footer">
        <Pagination :length="paginationCount" v-model="listPage" />
        <SnackbarSuccess v-model="snackbarSuccess" />
        <SnackbarError v-model="snackbarError" />
        <BtnTableHelp />
      </div>
    </template>
  </v-data-table>
</template>

<script>
import CardTitle from "@/components/CardTitle";
import BtnTableHelp from "@/components/BtnTableHelp";
import ActionBtn from "@/components/ActionBtn";
import Pagination from "@/components/Pagination";
import SnackbarSuccess from "@/components/SnackbarSuccess";
import SnackbarError from "@/components/SnackbarError";
import jalali from "moment-jalaali";

export default {
  name: "List",
  components: {
    CardTitle,
    BtnTableHelp,
    ActionBtn,
    Pagination,
    SnackbarSuccess,
    SnackbarError,
  },
  data() {
    return {
      listCounts: [5, 10, 20, 50, 100],
      listSearch: null,
      listPage: 1,
      listCount: 5,
      isLoading: false,
      snackbarSuccess: false,
      snackbarError: false,
      tableHeader: [
        {
          value: "icon",
          sortable: false,
        },
        {
          text: "نام ثبت کننده",
          value: "user.name",
          align: "center",
        },
        {
          text: "متن",
          value: "body",
          align: "center",
        },
        {
          text: "وضعیت",
          value: "status",
          align: "center",
        },
        {
          text: "تاریخ ثبت",
          value: "createdAt",
          align: "center",
        },

        {
          text: "عملیات",
          value: "action",
          align: "center",
          sortable: false,
        },
      ],
      tableData: [],
    };
  },

  methods: {
    jalali(str) {
      return jalali(str).format("jYYYY/jM/jD");
    },
    checkIsLoading() {
      this.tableData.length == 0
        ? (this.isLoading = true)
        : (this.isLoading = false);
    },
    filterCity() {
      this.tableData.forEach((item) => {
        item.city = this.allCities.find((city) => city.id == item.city);
      });
    },
    filterItem(item) {
      this.tableData = this.tableData.filter(
        (single) => single._id != item._id
      );
    },
    deleteItem(item) {
      this.$http
        .delete(`${this.$privateKey}/agent/${item._id}`)
        .then((res) => {
          item.deleteDialog = false;
          // this.filterItem(item);
          // this.checkIsLoading();
          // this.snackbarSuccess = true;
          return res;
        })
        .catch((err) => {
          this.snackbarError = true;
          return err;
        });
    },

    getTableData() {
      this.$http
        .get(
          `${process.env.VUE_APP_API_COMMENTS}/accepter/${this.$store.state.acceptor._id}`
        )
        .then((res) => {
          this.tableData = res.data.data;
        })
        .catch((err) => {
          this.snackbarError = true;
          console.error(err);
        });
    },
  },
  computed: {
    paginationCount() {
      let pageCount = this.tableData.length / this.listCount;
      let pageCountRound = Math.ceil(pageCount);
      return pageCountRound;
    },

    listCountsComputed() {
      let listCountFiltered = [];
      listCountFiltered = this.listCounts.filter(
        (item) => item <= this.tableData.length
      );

      if (this.tableData.length % 5 > 0 && 50 >= this.tableData.length) {
        listCountFiltered.push(this.listCounts[listCountFiltered.length]);
      }
      return listCountFiltered;
    },
  },

  async created() {
    await this.$http.get(`${process.env.VUE_APP_API_CITY}`).then((res) => {
      this.allCities = res.data;
    });

    await this.getTableData();
  },
};
</script>
<style>
.table__row {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
</style>