<template>
  <v-col :cols="cols" :sm="sm" :md="md" :lg="lg" :xl="xl">
    <slot name="before"></slot>
    <div class="d-flex mx-auto align-center">
      <v-btn
        :class="changeColor"
        class="btn-date-picker mx-auto"
        width="75%"
        height="45"
        @click="show = true"
      >
        <span class="white--text" v-show="!value">{{ label }}</span>
        <span class="white--text" v-show="value">{{ value }}</span>
        <span><IconDate width="20" /></span>
      </v-btn>
      <v-text-field
        type="text"
        class="form-control is-editable"
        placeholder="YYYY/MM/DD"
        id="my-custom-editable-input"
        v-show="false"
      >
      </v-text-field>

      <DatePicker
        :value="value"
        @input="$emit('input', $event)"
        color="#26caef"
        format="jYYYY/jMM/jDD"
        element="my-custom-editable-input"
        :editable="true"
        :show="show"
        @close="show = false"
        :rules="[rules.required]"
        required
      />
    </div>
    <slot name="after"></slot>
  </v-col>
</template>

<script>
import DatePicker from "vue-persian-datetime-picker";
import IconDate from "@/icons/Main/IconCalendar";
export default {
  name: "Date",
  components: {
    DatePicker,
    IconDate,
  },
  props: {
    value: { type: String, default: null },
    label: { type: String, default: "انتخاب تاریخ" },
    cols: { type: String, default: "12" },
    sm: { type: String, default: null },
    md: { type: String, default: null },
    lg: { type: String, default: null },
    xl: { type: String, default: null },
    validated: { type: Boolean, default: false },
  },
  data() {
    return {
      date: "",
      show: false,
      error: "blue",
      rules: {
        required: (value) => !!value || "این فیلد الزامی است",
      },
    };
  },
  methods: {},
  computed: {
    changeColor() {
      return !this.value && this.validated ? "red" : "primary";
    },
  },
};
</script>

<style>
.btn-date-picker .v-btn__content {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 30px;
}
</style>