<template>
  <v-data-table
    class="elevation-1 data-table grey--text text--darken-2"
    hide-default-footer
    :headers="tableHeader"
    :items="tableData"
    :search="listSearch"
    :items-per-page="listCount"
    :page="listPage"
    :loading="isLoading"
    loading-text="در حال بار گذاری ..."
  >
    <template v-slot:top>
      <div style="background-color: #e9e9e9; position: relative">
        <CardTitle label="لیست تخفیفات پذیرنده ها" color="#d55789" />
        <v-row>
          <v-col cols="9" sm="4" offset-sm="6">
            <v-text-field
              v-model="listSearch"
              label="جستجو"
              append-icon="mdi-magnify"
            />
          </v-col>
          <v-col cols="3" sm="2">
            <v-select
              class="mx-5"
              :items="listCountsComputed"
              v-model="listCount"
            />
          </v-col>
        </v-row>
      </div>
    </template>

    <template v-slot:item.accepter.status="{ item }">
      <span class="red--text" v-show="item.accepter.status == -1">
        رد شده
      </span>
      <span class="warning--text" v-show="item.accepter.status == 0">
        در حال رسیدگی
      </span>
      <span class="success--text" v-show="item.accepter.status == 1">
        تائید شده
      </span>
    </template>

    <template v-slot:item.createdAt="{ item }">
      <div class="vira-fa">{{ jalali(item.createdAt) }}</div>
    </template>

    <template v-slot:item.action="{ item }">
      <v-row justify="center">
        <v-col cols="12" md="10" lg="8" xl="6">
          <v-row>
            <ActionBtn iconEye lg="4" @click="viewItem(item)" />
            <ActionBtn iconEdit @click="editItem(item)" lg="4" />
            <ActionBtn iconDelete @click="deleteItem(item)" lg="4" />
          </v-row>
        </v-col>
      </v-row>
    </template>

    <template v-slot:footer>
      <div class="vira-data-table-footer">
        <Pagination :length="paginationCount" v-model="listPage" />
        <SnackbarSuccess v-model="snackbarSuccess" />
        <SnackbarError v-model="snackbarError" />
        <BtnTableHelp />
      </div>
    </template>
  </v-data-table>
</template>

<script>
import CardTitle from "@/components/CardTitle";
import BtnTableHelp from "@/components/BtnTableHelp";
import ActionBtn from "@/components/ActionBtn";
import Pagination from "@/components/Pagination";
import SnackbarSuccess from "@/components/SnackbarSuccess";
import SnackbarError from "@/components/SnackbarError";
import jalali from "moment-jalaali";
export default {
  name: "List",
  components: {
    CardTitle,
    BtnTableHelp,
    ActionBtn,
    Pagination,
    SnackbarSuccess,
    SnackbarError,
  },
  data() {
    return {
      listCounts: [5, 10, 20, 50, 100],
      listSearch: null,
      listPage: 1,
      listCount: 5,
      isLoading: false,
      snackbarSuccess: false,
      snackbarError: false,
      tableHeader: [
        {
          text: "نام پذیرنده",
          value: "accepter.name",
          align: "center",
        },
        {
          text: "نام دسته بندی",
          value: "title",
          align: "center",
        },
        {
          text: "درصد تخفیف",
          value: "discount",
          align: "center",
        },
        {
          text: "وضعیت",
          value: "accepter.status",
          align: "center",
        },
        {
          text: "تاریخ ثبت",
          value: "createdAt",
          align: "center",
        },

        {
          text: "عملیات",
          value: "action",
          align: "center",
          sortable: false,
        },
      ],
      tableData: [],
      status: {
        false: "غیر فعال",
        true: "فعال",
      },
    };
  },

  methods: {
    checkIsLoading() {
      this.tableData.length == 0
        ? (this.isLoading = true)
        : (this.isLoading = false);
    },

    // deleteItem(item) {
    //   this.$http
    //     .delete(`${this.$privateKey}/accepter/${item._id}`)
    //     .then((res) => {
    //       item.deleteDialog = false;
    //       // this.filterItem(item);
    //       // this.checkIsLoading();
    //       this.snackbarSuccess = true;
    //       return res;
    //     })
    //     .catch((err) => {
    //       this.snackbarError = true;
    //       return err;
    //     });
    // },
    // editItem(item) {
    //   console.log(item.name);
    //   this.$router.push(`/admin/acceptor/${item._id}/edit`);
    // },
    jalali(str) {
      return jalali(str).format("HH:mm jYYYY/jM/jD");
    },
    viewItem(item) {
      this.$router.push(`/admin/acceptor/${item._id}/discount-view`);
    },


    getTableData() {
      return new Promise((resolve, reject) => {
        this.$http
          .get(process.env.VUE_APP_API_ACCEPTOR_DISCOUNT)
          .then((res) => {
            this.tableData = res.data.data;     
            // this.checkIsLoading();
            // this.snackbarSuccess = true;
            resolve(res);
          })
          .catch((err) => {
            this.snackbarError = true;
            reject(err);
          });
      });
    },
  },
  computed: {
    paginationCount() {
      let pageCount = this.tableData.length / this.listCount;
      let pageCountRound = Math.ceil(pageCount);
      return pageCountRound;
    },

    listCountsComputed() {
      let listCountFiltered = [];
      listCountFiltered = this.listCounts.filter(
        (item) => item <= this.tableData.length
      );

      if (this.tableData.length % 5 > 0 && 50 >= this.tableData.length) {
        listCountFiltered.push(this.listCounts[listCountFiltered.length]);
      }
      return listCountFiltered;
    },
  },

  async beforeMount() {
    await this.getTableData();
  },
};
</script>
