<template>
  <v-card class="">
    <div class="mb-10" style="background-color: #e9e9e9; position: relative">
      <CardTitle label="لیست حساب ها" color="#cf0e56" />

      <v-row>
        <v-col cols="9" sm="4" offset-sm="6" class="py-0">
          <v-text-field
            v-model="listSearch"
            label="جستجو"
            append-icon="mdi-magnify"
            @keyup="filterData"
          />
        </v-col>
        <v-col cols="3" sm="2" class="py-0">
          <v-select class="mx-5" :items="listCounts" v-model="listCount" />
        </v-col>
      </v-row>
    </div>
    <div class="px-10 pb-10">
      <v-row>
        <v-col cols="12" md="6" v-for="(card, index) in validData" :key="index">
          <v-card :color="card.color" class="bank-card">
            <v-responsive aspect-ratio="1.7">
              <div class="bank-card__name px-2 py-2" style="color: #686868">
                {{ card.name }}
              </div>
              <div
                class="bank-card__sheba text-center white--text d-flex align-center justify-center vira-en"
              >
                IR{{ card.sheba }}
              </div>
              <div>
                <v-dialog v-model="deleteDialog" persistent max-width="390">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn icon v-bind="attrs" v-on="on">
                      <IconDelete width="18" height="18" />
                    </v-btn>
                  </template>
                  <v-card>
                    <v-card-title class="headline primary white--text" >
                      حذف اطلاعات
                    </v-card-title>
                    <v-card-text class="py-5"
                      >آیا از حذف حساب مطمئن هستید؟</v-card-text
                    >
                    <v-card-actions class="py-5">
                      <v-spacer></v-spacer>
                      <v-btn
                        color="primary"
                        rounded
                        min-width="120"
                        height="42"
                        @click="deleteItem(card)"
                      >
                        تائید
                      </v-btn>
                      <v-spacer></v-spacer>
                      <v-btn
                      color="red"
                        rounded
                        min-width="120"
                        height="42"
                        dark
                        @click="deleteDialog = false"
                      >
                        انصراف
                      </v-btn>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </div>
            </v-responsive>
          </v-card>
        </v-col>
      </v-row>
    </div>

    <div class="vira-data-table-footer">
      <Pagination :length="paginationCount" v-model="listPage" />
    </div>
  </v-card>
</template>

<script>
import IconDelete from "@/icons/Main/IconDelete";
import Pagination from "@/components/Pagination";
import CardTitle from "@/components/CardTitle";
export default {
  name: "List",
  components: {
    IconDelete,
    Pagination,
    CardTitle,
  },
  data() {
    return {
      deleteDialog: false,
      listCounts: [4, 8],
      listSearch: null,
      listPage: 1,
      listCount: 4,
      tableData: [],
      totalTableData: [],
      colors: [
        { id: 0, color: "#02aae8" },
        { id: 1, color: "#5613a2" },
        { id: 2, color: "#f25920" },
        { id: 3, color: "#f0c239" },
        { id: 4, color: "#7c4789" },
        { id: 5, color: "#00cf95" },
        { id: 6, color: "#364968" },
        { id: 7, color: "#ca6144" },
      ],
      datee: new Date(0),
    };
  },
  methods: {
    deleteItem(item) {
      this.$http
        .delete(`${process.env.VUE_APP_API_ACCOUNT}/${item._id}`)
        .then((res) => {
          this.deleteDialog = false;
          this.tableData.splice(
            this.tableData.findIndex(
              (singleItem) => singleItem._id == item._id
            ),
            1
          );

          return res;
        })
        .catch((err) => {
          return err;
        });
    },
    getRandomInt(max) {
      return Math.floor(Math.random() * Math.floor(max));
    },
    getTableData() {
      this.$http
        .get(process.env.VUE_APP_API_ACCOUNT)
        .then((res) => {
          this.tableData = res.data.data;
          this.totalTableData = res.data.data;
        })
        .catch((err) => {
          this.snackbarError = true;
          console.error(err);
        });
    },
    filterData() {
      this.tableData = this.totalTableData.filter(
        (single) =>
          single.sheba.includes(this.listSearch) ||
          single.name.includes(this.listSearch)
      );
    },
  },
  mounted() {
    this.getTableData();
  },
  computed: {
    filteredList() {
      return this.tableData.filter((single) => {
        return (
          single.sheba.includes(this.listSearch) ||
          single.name.includes(this.listSearch)
        );
      });
    },
    validData() {
      const result = this.tableData.filter((item, index) => {
        if (
          index >= this.listCount * (this.listPage - 1) &&
          index <= this.listCount * this.listPage - 1
        )
          return item;
      });
      result.forEach((single, index) => {
        single.color = this.colors.find((color) => color.id == index).color;
      });
      return result;
    },
    paginationCount() {
      let pageCount = this.tableData.length / this.listCount;
      let pageCountRound = Math.ceil(pageCount);
      return pageCountRound;
    },
  },
};
</script>
<style lang="scss">
.bank-card {
  &__name {
    background-color: #e1e1e1;
  }
  .v-responsive__content {
    display: grid;
    grid-template-rows: max-content 1fr max-content;
  }
}
</style>