<template>
  <v-app
    :class="{
      'vira-bg': $vuetify.breakpoint.smAndUp,
    }"
  >
    <v-main class="align-center">
      <v-container>
        <v-row justify="center" align="center">
          <v-col cols="12" md="7" lg="4" xl="3">
            <v-form
              class="white rounded-lg-2 py-lg-8 px-lg-6"
              ref="thisForm"
              lazy-validation
              @submit.prevent="validate"
            >
              <v-row justify="center">
                <v-col cols="12" lg="8">
                  <v-img src="@/assets/logo.svg" lazy-src="@/assets/logo.svg" />
                </v-col>
              </v-row>
              <v-row>
           
                  <English label="نام کاربری" v-model="login.username" />
      

             
                  <Password v-model="login.password" />
            
              </v-row>
              <v-row>
                <v-col cols="12">
                  <v-checkbox
                    label="مرا بخاطر بسپار"
                    color="#0d8dbf"
                    v-model="login.remmber"
                  />
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <div class="d-flex justify-space-around">
                    <Btn color="#57cf8f" dark type="submit">
                      <template slot="label">
                        ورود
                      </template>
                      <template slot="after">
                        <IconKey width="18" height="18" />
                      </template>
                    </Btn>
                    <Btn color="#4fc7db" dark to="/register">
                      <template slot="label">
                        ثبت نام
                      </template>
                      <template slot="after">
                        <IconUser width="18" height="18" />
                      </template>
                    </Btn>
                  </div>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <Btn
                    text
                    color="#0d8dbf"
                    label="رمز عبور را فراموش کرده‌ام"
                    dark
                    to="/forget"
                    class="mx-auto"
                  />
                </v-col>
              </v-row>

              <v-snackbar
                v-model="snackbarSuccess"
                color="green"
                top
                left
                :timeout="1000"
                content-class="text-center"
              >
                ورود با موفقیت انجام شد
              </v-snackbar>
              <v-snackbar
                v-model="snackbarError"
                color="red"
                top
                left
                :timeout="1000"
                content-class="text-center"
              >
                ورود ناموفق
              </v-snackbar>
            </v-form>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import English from "@/components/English";
import Password from "@/components/Password";
import Btn from "@/components/Btn";
import IconKey from "@/icons/Main/IconKey";
import IconUser from "@/icons/Main/IconUser"
export default {
  name: "Login",
  components: {
    English,
    Password,
    Btn,
    IconKey,
    IconUser
  },

  data() {
    return {
      snackbarSuccess: false,
      snackbarError: false,
      login: {
        username: "",
        password: "",
        remmber: false,
      },
      rules: {
        required: (value) => !!value || "این فیلد الزامی است",
      },
    };
  },
  methods: {
    validate() {
      if (this.$refs.thisForm.validate()) {
        this.loginFunc();
      }
    },

    loginFunc() {
      let auth = this.login.username;
      let password = this.login.password;
      let remmber = this.login.remmberPassword;
      return new Promise((resolve, reject) => {
        this.$store
          .dispatch("getToken", { auth, password, remmber })
          .then((res) => {
            this.snackbarSuccess = true;
            this.$router.push("/admin/dashboard");
            resolve(res);
          })
          .catch((err) => {
            this.snackbarError = true;
            reject(err);
          });
      });
    },
  },

  computed: {
    authMessage() {
      return this.$store.state.auth.message;
    },
    authStatus() {
      return this.$store.state.status;
    },
  },
};
</script>
