<template>
  <v-form ref="thisForm" lazy-validation @submit.prevent="validate">
    <v-card class="pa-16">
      <CardTitle label="ثبت حساب جدید" color="#f9ae17" />
      <v-row>
        <Texts md="3" label="نام حساب" v-model="account.name" />

        <Wallets md="4" label="نام کیف پول" v-model="account.walletId" />
      </v-row>
      <v-row justify="center">
        <v-col cols="12" md="8" class="d-flex justify-space-center">
          <TheMask
            v-model="account.sheba"
            class="vira-ltr masked-input"
            mask="## #### #### #### #### #### ##"
            placeholder="__ ____ ____ ____ ____ ____ __"
          />
          <span class="vira-ir">IR</span>
        </v-col>
      </v-row>
      <v-row justify="end" class="mt-16">
        <Btn color="primary" label="ثبت حساب" type="submit" />
      </v-row>

    </v-card>
  </v-form>
</template>

<script>
import CardTitle from "@/components/CardTitle";
import Texts from "@/components/Texts";
import Btn from "@/components/Btn";
import { TheMask } from "vue-the-mask";
import Wallets from "@/components/Wallets";

export default {
  components: { CardTitle, Texts, TheMask, Btn, Wallets },
  data() {
    return {
      account: {
        name: "",
        sheba: "",
        walletId: "",
        userId: this.$store.state.currentUser._id,
      },
    };
  },
  methods: {
    //////////////////////////////////
    // Start router to list
    routerToList() {
      setTimeout(() => {
        this.$router.push(`/acceptor/account/list`);
      }, 600);
    },
    //  End  router to list
    //////////////////////////////////
    //////////////////////////////////
    // Start check form validation
    validate() {
      if (this.$refs.thisForm.validate()) {
        this.submitForm();
      }
    },
    //  End  check form validation
    //////////////////////////////////

    //////////////////////////////////
    // Start
    submitForm() {
      this.$http
        .post(process.env.VUE_APP_API_ACCOUNT, this.account)
        .then(() => {
          this.snackbar = true;
          this.routerToList();
        })
        .catch(() => {
          this.error = true;
        });
    },
    //  End
    //////////////////////////////////
  },
};
</script>

<style>
.vira-ir {
  font-family: Arial;
  font-size: 1.7rem;
  color: #686868;
}
.masked-input {
  display: block;
  height: 45px;
  outline: none;
  width: 100%;
  font-family: Arial;
  font-size: 1.7rem;
  color: #686868;
}
</style>