<template>
  <v-form ref="thisForm" lazy-validation @submit.prevent="validateForm">
    <v-card class="pa-10">
      <v-row>
        <Title label="مشخصات نماینده" />
      </v-row>
      <v-row>

        <Texts
          md="4"
          label="نام و نام خانوادگی"
          v-model="agent.name"
          :validations="['required', 'persianCharachter']"
        />

        <Texts md="4" label="نام پدر" v-model="agent.fatherName" :validations="['required', 'persianCharachter']"/>
        <Number md="4" label="تلفن همراه" v-model="agent.mobile" />
      </v-row>
      <v-row>
        <Number md="4" label="کد ملی" v-model="agent.nationalCode" />
        <Number
          md="4"
          label="شماره شناسنامه"
          v-model="agent.certificateNumber"
        />
        <Email md="4" label="پست الکترونیکی" v-model="agent.email" />
      </v-row>
      <v-row>
        <Country md="3" v-model="agent.country" />
        <Province md="3" v-model="agent.state" :countryId="agent.country" />
        <City md="3" v-model="agent.city" :stateId="agent.state" />
        <Number md="3" label="کد پستی" v-model="agent.postalCode" />
      </v-row>
      <v-row>
        <Textarea
          label="آدرس محل سکونت"
          v-model="agent.address"
          className="rounded-lg-1"
        />
      </v-row>
      <v-row>
        <English md="4" label="تلفن ثابت" v-model="agent.phone" />
        <v-col cols="12" md="4">
          <v-select
            item-text="name"
            item-value="value"
            label="وضعیت"
            :rules="[rules.status]"
            :items="allStatus"
            v-model="agent.status"
            required
          />
        </v-col>
        <v-col cols="12" md="4">
          <v-select
            label="نوع نماینده"
            item-text="name"
            item-value="id"
            :rules="[rules.required]"
            :items="alltypes"
            v-model="agent.type"
            required
          />
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12" md="8">
          <v-row>
            <English md="6" label="نام کاربری" v-model="agent.username" />
            <Number
              md="6"
              label="کد کاربری معرف"
              v-model="agent.referenceCode"
            />
          </v-row>
          <v-row>
            <Password v-model="agent.password" md="6" />
            <PasswordRepeat
              v-model="agent.passwordRepeat"
              :forCheck="agent.password"
              md="6"
            />
          </v-row>
        </v-col>
        <v-col cols="12" md="4">
          <v-row>
            <DatePicker v-model="agent.birthdate" :validated="clicked" />
          </v-row>
        </v-col>
      </v-row>

      <v-row>
        <Gender v-model="agent.gender" />
      </v-row>

      <v-row>
        <Title label="بارگذاری تصاویر مدارک" />
      </v-row>
      <v-row>
        <ImageUploader
          :imgSrc="agent.nationalCardImage"
          imgName="nationalCardImage"
          md="4"
          required
          :validated="clicked"
        >
          <template v-slot:header> بارگذاری تصویر کارت ملی </template>
        </ImageUploader>
        <ImageUploader
          :imgSrc="agent.birthCertificateImage"
          imgName="birthCertificateImage"
          md="4"
          required
          :validated="clicked"
        >
          <template v-slot:header> بارگذاری تصویر شناسنامه </template>
        </ImageUploader>
      </v-row>
      <v-row>
        <v-col cols="12" class="d-flex justify-center mt-16">
          <Btn label="تايید نهایی" color="#57cf8f" dark type="submit">
            <template slot="after">
              <IconCheck width="23" height="23" />
            </template>
          </Btn>
        </v-col>
      </v-row>
    </v-card>
  </v-form>
</template>

<script>
import Title from "@/components/Title";
import Number from "@/components/EnglishNumber";
import Email from "@/components/Email";
import Country from "@/components/Country";
import Province from "@/components/Province";
import City from "@/components/City";
import Textarea from "@/components/Textarea";
import English from "@/components/English";
import DatePicker from "@/components/DatePicker";
import Gender from "@/components/Gender";
import Password from "@/components/Password";
import PasswordRepeat from "@/components/PasswordRepeat";
import ImageUploader from "@/components/ImgUploader";
import Btn from "@/components/Btn";
import IconCheck from "@/icons/Main/IconCheckedCircle";
import { EventBus } from "@/mixins/EventBus.js";
import Texts from "@/components/Texts";
export default {
  name: "Create",
  components: {
    Texts,
    Title,
    Number,
    Email,
    Country,
    Province,
    City,
    Textarea,
    English,
    DatePicker,
    Gender,
    Password,
    PasswordRepeat,
    ImageUploader,
    Btn,
    IconCheck,
  },
  data() {
    return {
      allStatus: [
        { id: 0, name: "غیر فعال", value: "-1" },
        { id: 2, name: "در حال رسیدگی", value: "0" },
        { id: 3, name: "فعال", value: "1" },
      ],
      statusList: [],

      alltypes: [
        { id: "0", name: "تکی" },
        { id: "1", name: "استانی" },
      ],
      agent: {
        city: 0,
        country: 0,
        state: 0,
        name: "",
        fatherName: "",
        mobile: "",
        nationalCode: "",
        certificateNumber: "",
        status: null,
        type: "",
        email: "",
        address: "",
        phone: "",
        username: "",
        postalCode: "",
        referenceCode: "",
        password: "",
        passwordRepeat: "",
        birthdate: "",
        gender: null,
        nationalCardImage: "",
        birthCertificateImage: "",
      },
      clicked: false,
      rules: {
        required: (value) => !!value || "این فیلد الزامی است",
        status: (value) =>
          !!value == "1" ||
          value == "0" ||
          value == "-1" ||
          "این فیلد الزامی است",
      },
    };
  },
  methods: {
    //////////////////////////////////
    // Start check form validation

    validateForm() {
      this.clicked = true;
      if (this.$refs.thisForm.validate()) {
        this.submitForm();
      }
    },

    //  End  check form validation
    //////////////////////////////////

    //////////////////////////////////
    // Start send data
    submitForm() {
      let formData = new FormData();
      let oFormData = this.agent;

      for (var key in oFormData) {
        if (oFormData[key] != null) formData.append(key, oFormData[key]);
      }

      this.$http
        .post(`${this.$privateKey}/agent`, this.agent)
        .then(() => {
   
          this.dialog = true;
          this.routerToList();
        })
        .catch(() => {
          this.error = true;
        });
    },
    //  End  send data
    //////////////////////////////////

    //////////////////////////////////
    // Start router to list
    routerToList() {
      setTimeout(() => {
        this.$router.push(`/admin/agent/list`);
      }, 600);
    },
    //  End  router to list
    //////////////////////////////////
  },
  mounted() {
    EventBus.$on("selectImg", (data) => {
      this.agent[data.name] = data.src;
    });
  },
  // computed: {
  //   ruless() {
  //     return [
  //       this.agent.gender.length > 0 || "At least one item should be selected"
  //     ];
  //   }
  // }
};
</script>
