<template>
  <v-app>
    <router-view />
  </v-app>
</template>

<script>
export default {
  name: "Asrevira",
  beforeCreate() {
    this.$http
      .get("/" + this.$store.state.privateKey + "/check-token")
      .then((res) => {
        let role = res.data.ASREVIRA.user.role;
        switch (role) {
          case "admin":
            this.$router.push("/admin/dashboard").catch((err) => {console.log('Catch : '+err);});
            break;
          case "agent":
            this.$router.push("/agent/dashboard").catch((err) => {console.log('Catch : '+err);});
            break;
          case "accepter":
            this.$router.push("/acceptor/dashboard").catch((err) => {console.log('Catch : '+err);});
            break;
          case "user":
            this.$router.push("/user/dashboard").catch((err) => {console.log('Catch : '+err);});
            break;
          default: 
            this.$router.push("/login").catch((err) => {console.log('Catch : '+err);});
        }
      })
      .catch((err) => {
        if (err.response.status == 403) {
          this.$store.dispatch("logout");
          this.$router.push("/login");
        }
      });
  },
};
</script>
