import Vue from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import vuetify from "./plugins/vuetify";
import "roboto-fontface/css/roboto/roboto-fontface.css";
import "@mdi/font/css/materialdesignicons.css";
import "@/assets/scss/style.scss";

Vue.config.productionTip = false;

import mixin from "@/mixins/global.js";
Vue.mixin(mixin);

//////////////////////////////////
// Start for Axios API
import Axios from "axios";
Vue.prototype.$http = Axios;
// Vue.prototype.$http.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
// Vue.prototype.$http.defaults.withCredentials = true;

Vue.prototype.$http.defaults.baseURL = process.env.VUE_APP_API_BASE;

const token = localStorage.getItem("_tk");
const publicKey = localStorage.getItem("_pb");
const accessUser = localStorage.getItem("_au");
if (token) {
  Vue.prototype.$http.defaults.headers.common["token"] = token;
  Vue.prototype.$http.defaults.headers.common["key_public"] = publicKey;
  Vue.prototype.$http.defaults.headers.common["access_user"] = accessUser;
}

//  End  for Axios API
//////////////////////////////////

