<template>
  <v-form ref="thisForm" lazy-validation @submit.prevent="validate">
    <v-row justify="center" class="mb-16">
      <v-col cols="12" lg="8" xl="6">
        <v-img
          src="@/assets/img/RegisterTab3.svg"
          lazy-src="@/assets/img/RegisterTab3.svg"
        />
      </v-col>
    </v-row>
    <v-row>
      <Title label="ارسال تصاویر پذیرنده" />
    </v-row>
    <v-row>
      <ImageUploader :imgSrc="imageSrc.logo" imgName="logo" md="4" required>
        <template v-slot:header> بارگذاری لوگو </template>
      </ImageUploader>
    </v-row>
    <v-row>
      <ImageUploader :imgSrc="imageSrc.pic1" imgName="pic1" md="4" />
      <ImageUploader :imgSrc="imageSrc.pic2" imgName="pic2" md="4" />
      <ImageUploader :imgSrc="imageSrc.pic3" imgName="pic3" md="4" />
      <ImageUploader :imgSrc="imageSrc.pic4" imgName="pic4" md="4" />
      <ImageUploader :imgSrc="imageSrc.pic5" imgName="pic5" md="4" />
    </v-row>
    <v-row>
      <ImageUploader
        :imgSrc="imageSrc.nationalCodeCard"
        imgName="nationalCodeCard"
        md="4"
        required
      >
        <template v-slot:header> بارگذاری تصویر کارت ملی </template>
      </ImageUploader>
      <ImageUploader
        :imgSrc="imageSrc.birthCertificate"
        imgName="birthCertificate"
        md="4"
        required
      >
        <template v-slot:header> بارگذاری تصویر شناسنامه </template>
      </ImageUploader>
      <ImageUploader
        :imgSrc="imageSrc.businessLicense"
        imgName="businessLicense"
        md="4"
        required
      >
        <template v-slot:header> بارگذاری پروانه کسب مربوطه </template>
      </ImageUploader>
      <ImageUploader
        :imgSrc="imageSrc.ownershipDocument"
        imgName="ownershipDocument"
        md="4"
        required
      >
        <template v-slot:header> بارگذاری اجاره نامه یا سند مالکیت </template>
      </ImageUploader>
      <ImageUploader :imgSrc="imageSrc.bills" imgName="bills" md="4" required>
        <template v-slot:header> بارگذاری قبض (برق، آب و...) </template>
      </ImageUploader>
    </v-row>

    <v-row>
      <ImageUploader :imgSrc="imageSrc.contract1" imgName="contract1" md="4" />
      <ImageUploader :imgSrc="imageSrc.contract2" imgName="contract2" md="4" />
      <ImageUploader :imgSrc="imageSrc.contract3" imgName="contract3" md="4" />
      <ImageUploader :imgSrc="imageSrc.contract4" imgName="contract4" md="4" />
      <ImageUploader :imgSrc="imageSrc.contract5" imgName="contract5" md="4" />
      <ImageUploader :imgSrc="imageSrc.contract6" imgName="contract6" md="4" />
      <ImageUploader :imgSrc="imageSrc.contract7" imgName="contract7" md="4" />
    </v-row>
    <v-row justify="space-around">
      <BtnBack label="بازگشت" dark class="mt-16" @click="btnBack" />

      <Dia btnClass="mt-16" color="#57cf8f"  />
    </v-row>
  </v-form>
</template>

<script>
import { EventBus } from "@/mixins/EventBus.js";
import Title from "@/components/Title";
import BtnBack from "@/components/BtnBack";
import ImageUploader from "@/components/ImgUploader";
import Dia from "@/components/Dia";
export default {
  name: "Images",
  components: {
    Title,
    ImageUploader,
    BtnBack,
    Dia,
  },
  data() {
    return {
      imageSrc: {
        logo: "",
        pic1: "",
        pic2: "",
        pic3: "",
        pic4: "",
        pic5: "",
        nationalCodeCard: "",
        birthCertificate: "",
        businessLicense: "",
        ownershipDocument: "",
        bills: "",
        contract1: "",
        contract2: "",
        contract3: "",
        contract4: "",
        contract5: "",
        contract6: "",
        contract7: "",
      },
    };
  },
  methods: {
    //////////////////////////////////
    // Start router to list 
    routerToList() {
      setTimeout(() => {
        this.$router.push(`/admin/acceptor/list`);
      }, 600);
    },
    //  End  router to list 
    //////////////////////////////////
    //////////////////////////////////
    // Start check form validation

    validate() {
      if (this.$refs.thisForm.validate()) {
        this.assign();
      }
    },

    //  End  check form validation
    //////////////////////////////////

    //////////////////////////////////
    // Start send data to Parent data
    assign() {
      this.$parent.$parent.create = {
        ...this.$parent.$parent.create,
        ...this.imageSrc,
      };

      this.submitForm();
    },
    //  End send data to Parent data
    //////////////////////////////////
    ////////////////8e24a85b845a40d42c9cdee7d51352679e3ca949//////////////////
    // Start brn next form
    btnBack() {
      this.$parent.$parent.step = 2;
      window.scrollTo(0, 0);
    },
    //  End  brn next form
    //////////////////////////////////

    //////////////////////////////////
    // Start send data
    submitForm() {
      let formData = new FormData();
      let oFormData = this.$parent.$parent.create;

      for (var key in oFormData) {
        if (oFormData[key] != null) formData.append(key, oFormData[key]);
      }
      // console.log(
      //   `I'm here :  ${this.$privateKey}/accepter , ${this.$parent.$parent.create}`
      // );

      this.$http
        .post(`${this.$privateKey}/accepter`, this.$parent.$parent.create)
        .then(() => {
          this.dialog = true;
          this.routerToList();
        })
        .catch(() => {
          this.error = true;
        });
    },
    //  End  send data
    //////////////////////////////////
  },
  mounted() {
    EventBus.$on("selectImg", (data) => {
      this.imageSrc[data.name] = data.src;
    });
  },
};
</script>
