  },
  methods: {
    validate() {
      if (this.$refs.thisForm.validate()) {
        this.loginFunc();
      }
    },

    loginFunc() {
      let auth = this.login.username;
      let password = this.login.password;
      let remmber = this.login.remmberPassword;
      return new Promise((resolve, reject) => {
        this.$store
          .dispatch("getToken", { auth, password, remmber })
          .then((res) => {
            this.snackbarSuccess = true;
            // this.$router.push("/");
            switch (res.ASREVIRA.user.role) {
              case "admin":
                this.$router.push("/admin/dashboard");
                break;
              case "accepter":
                this.$router.push("/acceptor/dashboard");
                break;
              case "agent":
                this.$router.push("/agent/dashboard");
                break;
            }
            // let role = res.ASREVIRA.user.role;
            // switch (role) {
            //   case "admin":
            //     this.$router.push("/admin/dashboard").catch((err) => {
            //       console.log("Catch : " + err);
            //     });
            //     break;
            //   case "agent":
            //     this.$router.push("/agent/dashboard").catch((err) => {
            //       console.log("Catch : " + err);
            //     });
            //     break;
            //   case "accepter":
            //     this.$router.push("/acceptor/dashboard").catch((err) => {
            //       console.log("Catch : " + err);
            //     });
            //     break;
            //   case "user":
            //     this.$router.push("/user/dashboard").catch((err) => {
            //       console.log("Catch : " + err);
            //     });
            //     break;
            // }

            resolve(res);
          })
          .catch((err) => {
            this.snackbarError = true;
            console.log(err);
            reject(err);
          });
      });
    },
  },

  computed: {
    authMessage() {
      return this.$store.state.auth.message;
    },
    authStatus() {
      return this.$store.state.status;
    },
  },
};
</script>
